// ./layout/Header.js
import React from 'react';
import { Layout, Menu, theme } from 'antd';
import { CustomAnchor } from '../component/CustomAnchor';

const { Header: AntdHeader } = Layout;

export function CustomHeader() {
  const {
    token: { colorBgContainer },
  } = theme.useToken();

  return (
    <Layout>
      <AntdHeader
        style={{
          position: 'sticky',
          top: 0,
          zIndex: 1,
          width: '100%',
          display: 'flex',
          alignItems: 'center',
            background: '#FFFBE9',
        }}
      >
        <CustomAnchor />
      </AntdHeader>
    </Layout>
  );
}
