// ./components/CustomAnchor.js
import React from 'react';
import { Anchor } from 'antd';
import '../App.css';

const anchorStyle = {
    color: 'chocolate', // Set the desired color
};
export function CustomAnchor() {
    return (
        <div className='CustomAnchor-title'>
            <Anchor
                direction="horizontal"
                style={anchorStyle}
                items={[
                    {
                        key: 'part-1',
                        href: '#part-1',
                        title: <span style={{
                            fontFamily: 'cursive',
                            fontSize: '25px',
                            color: 'chocolate',
                        }}>Introduction</span>,
                    },
                    {
                        key: 'part-2',
                        href: '#part-2',
                        title: <span style={{
                            fontFamily: 'cursive',
                            fontSize: '25px',
                            color: 'chocolate',
                        }}>Project</span>,
                    },
                    {
                        key: 'part-3',
                        href: '#part-3',
                        title: <span style={{
                            fontFamily: 'cursive',
                            fontSize: '25px',
                            color: 'chocolate',
                        }}>Skills</span>,
                    },
                ]}
            />
        </div>
    );
};
