import { PlusCircleTwoTone } from '@ant-design/icons';
import { List } from 'antd';
import React from 'react';
import "../App.css";

export function Part3() {
    const listItemStyle = {
        marginBottom: '8px',
    };


    const iconStyle = {
        marginRight: '8px', // Adjust the margin as needed
    };

    return (
        <div className="part-3-content-container">
            <div className="skills-content-title">
                <label className="project-name">Skills</label>
            </div>

            <div className="skills-content">
                {/*  */}
                <div className="skills-content-name">
                    <label className="Card-About-content" ></label>
                    <List
                        grid={{ gutter: 16, column: 3 }}
                        dataSource={[
                            'Java',
                            'Spring',
                            'HTML',
                            'CSS',
                            'JavaScript',
                            'JQuery',
                            'Oracle',
                            'Mybatis',
                        ]}
                        renderItem={(item) => (
                            <List.Item style={{ ...listItemStyle, width: 'calc(33.33% - 16px)', textAlign: 'center' }}>
                                <PlusCircleTwoTone twoToneColor="#D3C9B5" style={iconStyle} />
                                <div className="item-font">{item}</div>
                            </List.Item>
                        )}
                    />
                </div>

                {/*    */}
                <div className="skills-content-second">
                    <label className="Card-About-content">  </label>
                    <List
                        grid={{ gutter: 16, column: 3 }}
                        dataSource={[
                            'Eclipse',
                            'IntelliJ',
                            'Git',
                            'GitHub',
                            'Sql Developer',
                            'VS Code',
                            'Notion',
                        ]}
                        renderItem={(item) => (
                            <List.Item style={{ ...listItemStyle, width: 'calc(33.33% - 16px)', textAlign: 'center' }}>
                                <PlusCircleTwoTone twoToneColor="#D3C9B5" style={iconStyle} />
                                <div className="item-font">{item}</div>
                            </List.Item>
                        )}
                    />
                </div>
            </div>
        </div>

    );
}
